import Dialog from "../Script/common/Dialog";
import cmd from "../Lobby.Cmd";
import InPacket from "../Script/networks/Network.InPacket";
import MiniGameNetworkClient2 from "../Script/networks/MiniGameNetworkClient2";
import Dropdown from "../Script/common/Dropdown";
import Configs from "../../../Loading/src/Configs";
import App from "../Script/common/App";
import Utils from "../Script/common/Utils";
import SPUtils from "../Script/common/SPUtils";
import Http from "../../../Loading/src/Http";
import Clipboards from "../Clipboards";
import BroadcastReceiver from "../Script/common/BroadcastReceiver";

import BaseTabShop from "./BaseTabShop";


//import VersionConfig from "../Script/common/VersionConfig";
//import ShootFishNetworkClient from "../Script/networks/ShootFishNetworkClient";
const { ccclass, property } = cc._decorator;
@ccclass
export default class TabBanks extends Dialog {
    @property(cc.Node)
    itemFactorTemplate: cc.Node = null;

    @property(cc.Label)
    lblBankNumber: cc.Label = null;
    @property(cc.Label)
    lblBankAccountName: cc.Label = null;

    @property(cc.Label)
    lblTransNote: cc.Label = null;
	@property(cc.Button)
    btnCopy: cc.Button = null;
	@property(cc.Button)
    btnCopyND: cc.Button = null;
    

    @property(Dropdown)
    dropdownBank: Dropdown = null;

    @property(cc.EditBox)
    edbAmount: cc.EditBox = null;

    @property(cc.Node)
    nodethongtin: cc.Node = null;

    @property(cc.Node)
    nodenap: cc.Node = null;

    @property(cc.Label)
    lblbanknganhang: cc.Label = null;

    @property(cc.Label)
    lblmoney: cc.Label = null;
    @property(cc.Label)
    lblnganhangname2: cc.Label = null;

	@property(cc.Sprite)
    qrCodeImage: cc.Sprite = null;


    private _listBank = [];

    start() {
        this.nodethongtin.active = true;
        this.nodenap.active = false;
        App.instance.showLoading(true);
        Http.get(Configs.App.Getbank, { "c": 130 }, (err, res) => {
            App.instance.showLoading(false);
            if (err) {
                console.error("Lỗi khi thực hiện yêu cầu HTTP:", err);
                return;
            }
            if (res.data && Array.isArray(res.data)) {
                res.data.forEach((bank, index) => {
                });
            } else {
                console.error("Dữ liệu không hợp lệ hoặc không có phần tử 'data'.");
            }

            if (res.data === undefined || res.data.length == 0) {
                console.warn("Không có ngân hàng nào trong phản hồi");
                return;
            }
    
            let listBank = res.data;
            this._listBank = listBank;
            let bankName = ["Chọn ngân hàng"];
            for (let i = 0; i < listBank.length; i++) {
                bankName.push(listBank[i].name); 
            }
            this.dropdownBank.setOptions(bankName);
            this.dropdownBank.setOnValueChange((idx) => {
                if (idx > 0) {
                    const selectedBank = listBank[idx - 1];
                    this.lblbanknganhang.string = selectedBank.code;
                } else {
                    this.lblBankAccountName.string = "";
                    this.lblBankNumber.string = "";
                }
            });
        });

		this.btnCopy.node.on("click", ()=> {
            if (this.lblBankNumber.string.length > 0) {
                
            SPUtils.copyToClipboard(this.lblBankNumber.string);
            App.instance.alertDialog.showMsg("Đã sao chép số tài khoản.");
        
            } else {
                App.instance.alertDialog.showMsg("Chưa có số tài khoản.");  
            }
        });
		this.btnCopyND.node.on("click", ()=> {
            if (this.lblTransNote.string.length > 0) {
                SPUtils.copyToClipboard(this.lblTransNote.string);
					App.instance.alertDialog.showMsg("Đã sao chép nội dung.");
                
            } else {
                App.instance.alertDialog.showMsg("Chưa có nội dung.");  
            }
        });
        
    }

    onClickCopyBankNumber() {
        Clipboards.copy(this.lblBankNumber.string);
        App.instance.ShowAlertDialog("Đã sao chép số tài khoản!");
    }

    onClickCopyBankContent() {
        Clipboards.copy(this.lblTransNote.string);
        App.instance.ShowAlertDialog("Đã sao chép nội dung!");
    }
	updateQRCode() {
        const bankId = this.lblnganhangname2.string;
        const accountNo = this.lblBankNumber.string;
        const accountName = this.lblBankAccountName.string;
        const amountStr = this.edbAmount.string.replace(/\./g, '');
        const amount = amountStr.trim() !== "" ? parseFloat(amountStr) : 0;
        const description = this.lblTransNote.string;

        if (!bankId || !accountNo || !accountName || !description) {
            return;
        }

        const qrCodeUrl = `https://api.vietqr.io/${bankId}/${accountNo}/${amount}/${encodeURIComponent(description)}/qOr8JLa.jpg?accountName=${encodeURIComponent(accountName)}`;
        cc.log("qrCodeUrl " + qrCodeUrl);
        cc.loader.load({ url: qrCodeUrl, type: 'jpg' }, (err, texture) => {
            if (err) {
                console.error(err);
                return;
            }
            this.qrCodeImage.spriteFrame = new cc.SpriteFrame(texture);
        });
    }


	onBtnXacNhan() {

        if (this.node.active) {
	        var money = Utils.formatEditBox(this.edbAmount.string);
            if (money < 20000) {
                App.instance.ShowAlertDialog(App.instance.getTextLang("txt_cash_in_min") + " 20.000");
                return;
            }
            if (money > 300000000) {
                App.instance.ShowAlertDialog(App.instance.getTextLang("txt_cash_in_max") + " 300.000.000");
                return;
            }
            var self = this;
            App.instance.showLoading(true, -1);
            var request = {
                "c": 2003,
                "fn": encodeURIComponent(this.lblbanknganhang.string),
                "am": money,
                "pt": 1,
                "nn": Configs.Login.Nickname,
                "at": Configs.Login.AccessToken,
                "pn": "manualbank",
                "bc": encodeURIComponent(this.lblbanknganhang.string),
                "ds": Configs.Login.Nickname,
                "bn": money
            };
            Http.get(Configs.App.API, request, (err, res) => {
                App.instance.showLoading(false);
                
                if (res.success == true) {
                    this.nodethongtin.active = false;
                    this.nodenap.active = true;

                  
                    const dataObject = JSON.parse(res.data);
                    cc.log(res.data);
                    let timeToExpire = dataObject.timeToExpired;
                    const phoneName1 = dataObject.account_name;
                    const bankNumber = dataObject.account;
                    const bankPorvider = dataObject.bank_code;
                    const bankCode = dataObject.content;

                     this.lblBankAccountName.string = phoneName1;
                     this.lblBankNumber.string = bankNumber;
                     this.lblTransNote.string = bankCode;
                     this.lblmoney.string = Utils.formatMoney2(money) + " VNĐ";
                     this.lblnganhangname2.string = bankPorvider;
                    this.updateQRCode();

                }
                else {
                    App.instance.ShowAlertDialog(res.message);
                }
            });
        }

    }

    
}

 